import React from 'react';
import { Container } from './styles';

interface Props extends React.HTMLAttributes<HTMLDivElement> {
  text: string;
  colorIcon?: string;
  backColor?: string;
  textColor?: string;
}

export const ButtonIcon: React.FC<Props> = ({
  children,
  text,
  colorIcon,
  backColor,
  textColor,
  className,
  onClick
}) => (
  <Container
    className={className}
    colorIcon={colorIcon}
    backColor={backColor}
    textColor={textColor}
    onClick={onClick}
  >
    {children}
    <span>{text}</span>
  </Container>
);
