import React from 'react';
import { UseFormRegisterReturn } from 'react-hook-form';
import { LabelCustom } from '../LabelCustom';
import {
  Container,
  IconWarning,
  Select,
  ArrowDown,
  WrappetSelect
} from './style';
import { KeyboardArrowDown } from '../../styles/Icons';

interface IProps {
  className?: string;
  register: UseFormRegisterReturn;
  errors?: any;
  label?: string;
  value?: string | number;
}

export const SelectForm: React.FC<IProps> = ({
  children,
  className,
  register,
  errors,
  label,
  value
}) => (
  <Container
    className={`${className} ${
      errors && errors[register.name] ? 'invalid' : ''
    }`}
  >
    <LabelCustom htmlFor={register?.name}>{label}</LabelCustom>
    <WrappetSelect>
      <Select
        // value={value || ''}
        name={register?.name}
        id={register?.name}
        onBlur={register?.onBlur}
        onChange={register?.onChange}
        ref={register?.ref}
      >
        {children}
      </Select>
      <span>
        <ArrowDown />
      </span>
    </WrappetSelect>
    <span>
      <IconWarning />
      {errors && errors[register.name]?.message}
    </span>
  </Container>
);
